$prefix: ".wc-booster";

#{$prefix}-wishlist-button-wrapper {
	display: flex;
	gap: 10px;
	z-index: 99;

	#{$prefix}-wishlist-button {
		padding: 5px;
		background: transparent;
		border: none;
		cursor: pointer;
		position: relative;
		font-family: inherit;
		.wishlist-text,
		.wishlist-text-left {
			position: absolute;
			top: 1px;
			right: -117px;
			visibility: hidden;
			opacity: 0;
			transition: all 0.5s ease-in-out;
			transform: translateX(20px);
			span {
				padding: 5px 10px;
				background-color: #2a2a2a7c;
				color: #fff;
				position: relative;
				font-size: 14px;
				&:before {
					content: "";
					position: absolute;
					left: -5px;
					top: 50%;
					width: 11px;
					height: 11px;
					background-color: rgba(42, 42, 42, 0.4862745098);
					clip-path: polygon(0 50%, 100% 100%, 100% 0);
					transform: translate(-50%, -50%);
				}
			}
		}
		&:hover {
			.wishlist-text,
			.wishlist-text-left {
				transition: all 0.5s ease-in-out;
				transform: translateX(0);
			}
		}
		.wishlist-text-left {
			right: unset;
			left: -117px;
			transform: translateX(-20px);
			span {
				&:before {
					left: unset;
					right: -16px;
					top: 50%;
					clip-path: polygon(100% 50%, 0 100%, 0 0);
					transform: translate(-50%, -50%);
				}
			}
		}

		i {
			font-weight: 900;
		}

		&.added {
			i {
				color: #e53737;
				font-weight: 900;

				&:before {
					content: "\f004";
				}
			}
		}

		&.loading {
			padding-right: 0 !important;

			i.fa-heart {
				animation: beat 0.25s infinite alternate;
				transform-origin: center;
			}
		}
	}
}

.products-block-post-template {
	li.product {
		position: relative;
	}
}
.wp-block-post-template {
	li.type-product {
		position: relative;
	}
}

.single-product {
	.wp-block-column {
		position: relative;
	}
}

.wp-block-woocommerce-product-collection .wc-block-product {
	position: relative;
}
